@charset "UTF-8";
html, body, div, span, h1, h2, h3, h4, h5, h6, p, a, strong, em, img, sub, sup, address, cite, pre, code, del, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, figure, figcaption, footer, header, menu, nav, output, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font: inherit;
	font-size: 100%;
	vertical-align: baseline;
}

ol, ul {
	list-style: none;
}

table {
	border-collapse: collapse;
	word-break: break-all;
}

button, html input[type='button'], input[type='reset'], input[type='submit'] {
	overflow: visible;
	-webkit-appearance: button;
	cursor: pointer;
}

input[type='number']::-webkit-inner-spin-button, input[type='number']::-webkit-outer-spin-button {
	height: auto;
}

input[type='search']::-webkit-search-cancel-button, input[type='search']::-webkit-search-decoration {
	-webkit-appearance: none;
}

html {
	overflow-x: hidden;
	width: 100%;
	height: 100%;
	font-size: 62.5%;
	-webkit-user-select: none;
	-webkit-text-size-adjust: none;
	-webkit-tap-highlight-color: transparent;
	-webkit-touch-callout: none;
}

body {
	width: 100%;
	font: 13px/1.3 ,'Gulim', '굴림', serif;
	letter-spacing: -.05em;
	word-break: keep-all;
	color: #111;
	background-color: #fff;
}

input, button, select, textarea {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 1em;
	font-family: inherit;
	color: inherit;
	box-sizing: border-box;
	-webkit-appearance: none;
	vertical-align: middle;
	border-radius: 0;
	background-color: transparent;
}

caption, legend, hr {
	display: none;
}

em, address, i {
	font-style: normal;
}

img {
	max-width: 100%;
	vertical-align: middle;
	-webkit-user-select: none;
}

a {
	color: inherit;
	text-decoration: none;
}

strong, b {
	font-weight: 700;
}

a, button, input, img, div, span {
	-webkit-tap-highlight-color: transparent;
	-webkit-touch-callout: none;
	outline: none;
}

.clear {
	content: '';
	display: block;
	clear: both;
}

.hidden {
	position: absolute;
	font: 0/0 a;
	visibility: hidden;
}

.align-c {
	text-align: center;
}

.align-l {
	text-align: left;
}

.align-r {
	text-align: right;
}

.valign-b {
	vertical-align: bottom;
}

.valign-t {
	vertical-align: top;
}

.valign-m {
	vertical-align: middle;
}

.margin-a {
	margin-left: auto;
	margin-right: auto;
